.App-intro {
    font-size: 1.4rem;
}

.container {
    display: -ms-flexbox;
    display: flex;
    list-style: none;
    margin: 0;
    padding: 0;
}

.container > li {
    -ms-flex: 1 1;
        flex: 1 1;

}

.container > li  a {
    border: 1px solid red;
    display: block;
    text-align: center;
}

@media all and (max-width: 400px) {
    .container > li {
        -ms-flex-preferred-size: 100%;
            flex-basis: 100%;
    }
    .search {
        -ms-flex-order: 1;
            order: 1;
    }
}

.marker {
    border: 10px solid red;
    width: 10px;
    height: 10px;
}


